.navbar {
    padding: 20px;
    display: flex;
    align-items: center;
    max-width: 600px;
    margin: 0 auto;
    border-bottom: 1px solid #f2f2f2;

    & h1 {
        color: #f1356d;
      }
      & .links {
        margin-left: auto;
      }
      & a {
        margin-left: 16px;
        padding: 6px;
        color: #a7224a;

        &:hover {
            color: #f1356d;
          }
      }
  }

  .content {
    max-width: 600px;
    margin: 40px auto;
    padding: 20px;
  }